.background {
  display: flex !important;
  position: fixed;
  z-index: 2001 !important;
  width: 35px !important;
  height: 35px !important;
  left: 20px !important;
  bottom: 20px !important;
  /* background: yellow !important; */ /* DEV */
  border-radius: 4px;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.buttonSettings {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  z-index: 2001 !important;
  padding: 0vw !important;
  min-width: 0vh !important;
  width: 35px !important;
  height: 35px !important;
}

.sliderBoxNews {
  display: flex;
  position: absolute;
  flex-direction : column-reverse;
  left: -514px;
  bottom: 49px;
  max-width: 500px;
  height: 168px;
  /* background:
    linear-gradient(
      to right,
      var(--coralNews) 0px, var(--coralNews) 80px,
      var(--greenNews) 80px, var(--greenNews) 500px
    );
  transition: --coralNews 1s, --greenNews 1s, left 1.5s; */
  background:
    linear-gradient(
      to right,
      lightcoral 0px, lightcoral 80px,
      lightgreen 80px, lightgreen 500px
    );
  transition: left 1.5s;
  /* transition: --coralNews 1s, --greenNews 1s, left 1.5s; */
}

.sliderBoxNews::before {
  position: absolute;
  content: "";
  inset: 0;
  /* background: linear-gradient(to bottom right, #2b2b2b 49.9%, #696868 50.1%); */
  background:
    linear-gradient(
      to right,
      #bd7979 0px, #bd7979 80px,
      #76b376 80px, #76b376 500px
    );
  opacity: 0;
  transition: opacity 1s;
}

.moveSliderBoxNews {
  left: -14px;
}

.buttonNews {
  display: flex;
  position: fixed;
  left: 30px;
  right: 800px;
  min-width: 30px !important;
  width: 30px !important;
  min-height: 120px !important;
  height: 120px !important;
  color: darkblue !important;
  font-size: 14px !important;
  border-radius: 0px !important;
  background: var(--buttonNews) !important;
}

.changeLogTypo {
  display: flex;
  font-family: Roboto;
  font-size: 13px;
  font-weight: 500;
  transform: rotate(270deg);
  text-wrap: nowrap;
}

.buttonsContainer {
  display: flex;
  position: absolute;
  top: -20px;
  flex-direction : row;
  /* background: orange; */ /* DEV */
  height: 20px;
}

.eachDescription {
  display: flex;
  flex-direction: row;
  position: relative;
  height: 24px;
  min-height: 24px;
  /* background:
    linear-gradient(
      to right,
      var(--coralNews) 0px, var(--coralNews) 80px,
      var(--greenNews) 80px, var(--greenNews) 500px
    ); */
  background:
    linear-gradient(
      to right,
      lightcoral 0px, lightcoral 80px,
      lightgreen 80px, lightgreen 500px
    );
  /* transition: --coralNews 1s, --greenNews 1s; */
}

.eachDescription::before {
  position: absolute;
  content: "";
  inset: 0;
  /* background: linear-gradient(to bottom right, #2b2b2b 49.9%, #696868 50.1%); */
  background:
    linear-gradient(
      to right,
      #bd7979 0px, #bd7979 80px,
      #76b376 80px, #76b376 500px
    );
  opacity: 0;
  transition: opacity 1s;
}

.eachDescription:hover::before  {
  opacity: 1;
}


