.date {
  display: flex;
  position: relative;
  justify-content: center;
  width: 80px;
  min-width: 80px;
  font-size: 16px;
}

.text {
  display: flex;
  position: relative;
  width: 420px;
  overflow-y: hidden;
  overflow-x: hidden;
  font-size: 16px;
  text-wrap: nowrap;
  white-space: nowrap;
}

button {
  border-radius: 2px;
  font-size: 14px;
  line-height: 14px;
  padding-top: 0px;
}

button:disabled {
  background: rgb(87, 87, 87);
  border-radius: 2px;
  border-color: rgb(87, 87, 87);
}

@media screen
and (width < 543px) {
  .text {
    width: calc(100vw - 92px);
  }
}